import style from 'styled-components'

export const InfoContainer = style.div`
    color: #fff;
    background: ${({lightBg}) => (lightBg ? '#d4f1f9' : '#1a237e')};

    @media screen and (max-width: 768px) {
        padding: 100px 0;
    }
`

export const InfoWrapper = style.div`
    display: grid;
    z-index: 1;
    height: 860px;
    width: 100%;
    max-width: 1100px;
    margin-right: auto;
    margin-left: auto;
    padding: 0 24px;
    justify-content: center;

`
export const InfoRow = style.div`
    display: grid;
    grid-auto-columns: minmax(auto, 1fr);
    align-items: center;
    grid-template-areas: ${({imgStart}) => (imgStart ? `'col2 col1'` : `'col1 col2'`)};

    @media screen and (max-width: 768px) {
        grid-template-areas: ${({imgStart}) => (imgStart ? `'col1 col2'` : `'col1 col1' 'col2 col2'`)};
    }
`
export const Column1 = style.div`
    margin-bottom: 15px;
    padding: 0 15px;
    grid-area: col1;
`
export const Column2 = style.div`
    margin-bottom: 15px;
    padding: 0 15px;
    grid-area: col2;
`

export const TextWrapper = style.div`
    max-width: 540px;
    padding-top: 0;
    padding-bottom: 60px;

`
export const TopLine = style.p`
    color: #01bf71;
    font-size: 16px;
    line-height: 16px;
    font-weight: 700;
    letter-spacing: 1.4px;
    text-transform: uppercase;
    margin-bottom: 16px;

`

export const Heading = style.h1`
    font-size: 48px;
    line-height: 1.1;
    font-weight: 600;
    margin-bottom: 24px;
    color: ${({lightText}) => (lightText ? '#C6CFD6' : '#010606')};

    @media screen and (max-width: 480px) {
        font-size: 32px;
    }
`
export const SubTitle = style.p`
    max-width: 440px;
    font-size: 18px;
    line-height: 24px;
    margin-bottom: 35px;
    color: ${({darkText}) => (darkText ? '#010606' : '#C6CFD6')};

`

export const BtnWrap = style.div`
    display: flex;
    justify-content: flex-start;
`

export const ImgWrap = style.div`
    max-width: 555px;
    height: 100%;
`

export const Img = style.img `
    width: 100%;
    margin: 0 0 10px 0;
    padding-right: 0;
`
