import styled from 'styled-components';
import { MdKeyboardArrowRight, MdArrowForward } from 'react-icons/md';


export const HeroContainer = styled.div `
    background: #1a237e;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    height: 800px;
    position: relative;
    z-index: 1;

    :before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: linear-gradient(180deg, rgba(0,0,0,0.2) 0%,
         rgba(0,0,0,0.6) 100%), linear-gradient(180deg, rgba(0,0,0,0.2) 0%, 
         transparent 100%);
        z-index: 2;
    }
`;

export const HeroBg = styled.div `
    position: absolute;
    top: 0px;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
`;
export const VideoBg = styled.video `
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    background-color: #1a237e;
    margint-top: 10px;
    opacity: 0.55;
`;
export const HeroContent = styled.div `
    z-index: 3;
    max-width: 1200px;
    position: Absolute;
    padding: 8 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
`
export const HeroH1 = styled.h1 `
    font-size: 48px;
    text-align: center;   
    color:  #CFD8DC;
    font-weight: bold;

    @media screen and (max-width: 768px) {
        font-size: 40px;
    }

    @media screen and (max-width: 480px) {
        font-size: 32px;
    }    
`
export const HeroP = styled.p `
    font-size: 28px;
    text-align: center;
    color: #CFD8DC;
    max-width: 600px;
    font-weight: semi-bold;
    margin-bottom: 20px;
    margin-top: 24px;
    
   

    @media screen and (max-width: 768px) {
        font-size: 24px;
    }

    @media screen and (max-width: 480px) {
        font-size: 18px;
    }
`
export const HeroBtnWrapper = styled.div `
    margin-top: 32px;
    display: flex;
    flex-direction: column;
    align-items: center;

`;
export const ArrowForward = styled (MdArrowForward) `
    color: #010606;
    margin-left: 8px;
    font-size: 20px;
`
export const ArrowRight = styled (MdKeyboardArrowRight) `
    color: #10606;
    margin-left: 8px;
    font-size: 20px; 
`